	onStoryChange (e)
	{

        const self = this;
        if (e.currentTarget.dataset.story==="top")
        {
        	self.gettop();
        }


		this.setState({ activeStory: e.currentTarget.dataset.story });
		if (e.currentTarget.dataset.story!="top") window.scrollTo(0, 0)
		if (e.currentTarget.dataset.story=="lk ") window.scrollTo(0, 0);
		//alert(e.currentTarget.dataset.story);
		if (this.state.activeStory==="top")
		{
			if (e.currentTarget.dataset.story==="lk")
			{
				window.scrollTo(0, 0);
			}

		}


		if (this.state.activeStory==="oferta")
		{
			if (e.currentTarget.dataset.story==="top")
			{
				window.scrollTo(300, 300);
			}
		}

		if (this.state.activeStory==="pricelist")
		{
			if (e.currentTarget.dataset.story==="top")
			{
				window.scrollTo(0, 0);
			}
		}
	}

	componentDidMount() {
		const self = this;


		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					self.setState({ fetchedUser: e.detail.data });
					self.setState({ vkid: e.detail.data.id });
					//self.checkvar("start");
					self.get_question();
					break;
				default:
					//console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

